import React from "react";
import "../styles/ContactForm.scss";
import { nameIcon, telephoneIcon, emailIcon, messageIcon, questionIcon } from "./Constants";

const ContactForm = props => {

    
    return <div className = "contact-form">
         <form className = "contact-form__form" >
            <div className = "contact-form__title">{props.formTitle}</div>
            
            <div className = "contact-form__field-container">
                <i className = {nameIcon}></i>
                <div>{props.nameLabel}</div>
                <input type="text"/>
            </div>
            <div className = "contact-form__field-container">
                <i className = {telephoneIcon}></i>
                <div>{props.phoneLabel}</div>
                <input type="text"/>
            </div>
            <div className = "contact-form__field-container">
            <i className = {emailIcon}></i>
                <div>{props.emailLabel}</div>
                <input type="text"/>
            </div>
            <div className = "contact-form__field-container">
                <i className = {questionIcon}></i>
                <div>{props.productLabel}</div>
                <div className = "contact-form__radio-container">
                    <input id = "radioTandem" type = "radio" name = "contact" value = "tandem"></input>
                    <label for = "radioTandem">{props.tandemLabel}</label>
                    <input id = "radioPac" type = "radio" name = "contact" value = "pac"></input>
                    <label for = "radioPac">{props.pacLabel}</label>
                </div>
            </div>
            <div className = "contact-form__field-container">
                <i className = {messageIcon}></i>
                <div>{props.messageLabel}</div>
                <textarea rows="5"></textarea>
            </div>
            <button><i class="fas fa-paper-plane"></i></button>
      </form>
    </div>
    
}

export default ContactForm;
